<template>
  <div id="app" :class="[mode]">
    <header>

    </header>
    <main>
      <div class="container">
        <div class="logo">
            <img src="./assets/logo.png">
        </div>
        <form v-on:submit.prevent>

          <div class="title">
            <h1> {{ form_title }} </h1>
            <p>
                晨悅預防醫學機構-晨悅診所成立於2019年，為中部地區高規格預防醫學機構， 透過專業預防醫學之醫護團隊的豐富經驗，提供舒適、隱私、高品質、 高效率且全方位的健康管理服務
            </p>
          </div>

          <div class="questions">

            <div v-for="field in form_config" class="question_field" :class="{hasError: error_fields[field.name], isEmpty: empty_fields[field.name]}">
              <div class="question_title"> {{ field.title }} <em if="field.require" class="require_mark">*</em> </div>

              <div class="input_field" :class="[field.type]">
                <template v-if="'text' === field.type">

                  <input type="text" v-model.lazy="form_values[field.name]" :name="field.name" :placeholder="field.placeholder" :require="field.require" :error="error_fields[field.name]" @change="validat({field_name:field.name, type:field.type, isRequire:field.require, rule:field.rule})">

                </template>


                <template v-if="'date' === field.type">
                  
                  <input type="date" v-model="form_values[field.name]" :name="field.name" :require="field.require">
                  
                </template>

                <template v-if="'radio' === field.type">
                  
                    <ul class="options">
                        <li v-for="opt in field.option" class="option">
                            <label>
                                <input type="radio" v-model="form_values[field.name]" :value="opt.value" :name="field.name">

                                <span> {{ opt.label }} </span>

                                <span v-if="'others' === opt.type">
                                    <input type="text" v-model="form_values[field.name]" :placeholder="opt.config.placeholder" >
                                </span>
                            </label>
                        </li>
                    </ul>

                </template>

              </div>

                
                <div v-if="field.error_msg" class="error_msg"> {{ field.error_msg }} </div>
                <div class="empty_msg"> 必填 </div>

            </div>

          </div>

          <div class="form_action">
            <button @click="handleSubmit"> 送出预约 </button>
          </div>

        </form>

      </div>
    </main>

    <footer>

    </footer>

    <div class="message_dialog" @click="mode=''">
        <div class="dialog_container">
            {{ message }}
        </div>
    </div>
  </div>
</template>

<script>
export default {
    name: 'app',
    data() {
        return {
            form_values: {},

            error_fields: {},

            empty_fields: {},

            // require_fields: {}

            mode: '',
            message: '',
        }
    },
    computed: {
        form_title() {
            return this.$store.state.form.title;
        },
        form_config() {
            return this.$store.state.form.config;
        },
        require_field() {
            return this.form_config.filter((field) => {
                return field.require;
            } );
        },
        lost_fields() {
            return this.require_field.filter( (field) => {
                return !this.form_values[field.name];
            } );
        },
        hasLost() {
            return !!Object.keys(this.lost_fields).find( (name) => {
                return this.lost_fields[name];
            } );
        },
        hasError() {
            return !!Object.keys(this.error_fields).find( (name) => {
                return this.error_fields[name];
            } )
        },
    },
    methods: {
        validat( { field_name = '', type, isRequire, rule } ) {
            let hasError = false;
            let empty = false;

            if ( isRequire && !this.form_values[field_name]) {
                empty = true;
            }
            this.$set(this.empty_fields, field_name, empty );

            if ( ['text'].includes(type) ) {
                
                hasError = !!rule.find( ( pattern ) => {
                    return !(new RegExp( `^${pattern}$`, 'g' )).test( this.form_values[field_name] );
                } )
            }

            this.$set(this.error_fields, field_name, hasError );

            // return hasError;
        },
        reset_form() {
            this.form_values = {};
            this.error_fields = {};
            this.empty_fields = {};
        },
        async handleSubmit() {
            this.form_config.forEach( (field) => {
                const { name:field_name, type, require:isRequire=false, rule=[] } = field;
                this.validat( { field_name, type, isRequire, rule } );
            } )

            if ( this.hasLost ) {
                this.message = '請填寫必填欄位';
                this.mode = 'dialog_active';

                return;
            }

            if ( this.hasError ) {
                this.message = '欄位錯誤，請正確填寫';
                this.mode = 'dialog_active';

                return ;
            }

            
            const {data, response:{status=422}} = await this.$http.post( '/api/mornjoy_register', this.form_values);

            console.log( status, data );

            if ( [401,422].includes(status) ) {
                // this.reset_form();

                this.message = '欄位錯誤，請正確填寫';
                this.mode = 'dialog_active';

                return;
            }

            this.message = '報名成功！';
            this.mode = 'dialog_active';

        }
    },
    created() {
        this.form_config.forEach(({ name }) => {
            this.$set(this.form_values, name, '');
        });
    }
}
</script>

